* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.search-form {
  text-align: center;
  background-color: #c9fefe;
  margin-left: auto;
  margin-right: auto;
}

.search-form input, .search-btn {
  border: 2px solid #084f4e;
  border-radius: 5px;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px 12px;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  justify-content: center;
  margin-top: 12px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 0;
  display: grid;
}

.photo-card {
  flex-direction: column;
  display: flex;
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

img {
  max-width: 100%;
  object-fit: cover;
  flex-grow: 1;
  display: block;
}

.photo-card .info {
  justify-content: space-between;
  display: flex;
}

.info-item {
  text-align: center;
  font-size: 14px;
}

.load-more {
  color: #1c0a24;
  background-color: #0ff;
  border: 2px solid #084f4e;
  border-radius: 5px;
}

.end-img {
  color: #da3a25;
  font-weight: bold;
}

.none-load-more {
  display: none;
}

